import React, {useState} from "react";
import Box from "@mui/material/Box";
import Card from "@mui/material/Card";
import Typography from "@mui/material/Typography";
import TypographyTitle from "./pixyComponents/typography/TypographyTitle";
import TypographyBody from "./pixyComponents/typography/TypographyBody";

interface HowItWorksStepProps {
  step: number;
  title: string;
  description: string;
}

// TODO: Style these properly
export const HowItWorksStep = ({
                                 step,
                                 title,
                                 description
                               }: HowItWorksStepProps) => {
  return (
    <Box className="flex"
     sx={{ 
      textAlign:"center",
      ['@media(min-width: 900px)'] : {
        width: '330px'
      },
      ['@media(max-width: 780px)'] : {
        marginBottom: '20px'
      }
      }}>
      <Typography
        sx={{
          fontSize: 120,
          fontWeight: 800,
          lineHeight: "55px",
          color: "white",
          marginBottom: "20px",
          opacity: "0.3" 
        }}
      > 
        {step}
      </Typography>
      <TypographyTitle sx={{ 
          color: "white",
          fontSize: "24px",
          fontWeight:500,
          lineHeight: "25px",
          margin: "20px 35px 0px 35px",
          textAlign: "left"
        }}>
        {title}
      </TypographyTitle>
      {/* <Typography sx={{ 
          color: "hsla(0, 0%, 100%, 0.7)",
          fontSize: "18px",
          fontWeight: 300,
          lineHeight: "25px",
          margin: "20px 35px 0px 35px"
        }}>
        {description}
      </Typography> */}
    </Box> 
  );
};
