import React from "react";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import { PageSection } from "../../containers/PageSection";
import { MockVideoDatastore } from "../../MockVideoDatastore";
import TypographyTitle, { TYPOGRAPHY_TITLE_KIND } from "../../components/pixyComponents/typography/TypographyTitle";
import { PixyVideoCard } from "../../components/pixyComponents";

const FeaturedProjects = () => {
  return (
    <PageSection id="featured-projects">
      <Box sx={{ my: 4, display: "flex", justifyContent: "center" }}>
        <TypographyTitle kind={TYPOGRAPHY_TITLE_KIND.Secondary}>
          Featured Projects
        </TypographyTitle>
      </Box>
      <Grid container spacing="12">
        {Object.keys(MockVideoDatastore).map((key: string) => {
          const { image, ...rest } = MockVideoDatastore[key];
          return <Grid item xs={12} md={12}><PixyVideoCard key={image} image={image} {...rest} /></Grid>;
        })}
      </Grid>
    </PageSection>
  );
};

export default FeaturedProjects;
