import React from "react";
import Box from "@mui/material/Box";

export interface PageSectionProps {
  id: string;
  sx: any;
}

export const PageSection = ({ flex, sx = {}, children, ...rest }: any) => {
  return (
    <Box
      className={`${!flex ? "" : "flex flex-stretch w-full"}`}
      component="section"
      sx={{
        ...sx,
        // height: "95.5vH",
        backgroundColor: "transparent",
        padding: "0px 20px",
      }}
      {...rest}
    >
      {children}
    </Box>
  );
};
