import React from "react";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import Typography from "@mui/material/Typography";
import { PageSection } from "../../containers/PageSection";
import TypographyTitle, { TYPOGRAPHY_TITLE_KIND } from "../../components/pixyComponents/typography/TypographyTitle";
import { HowItWorksStep } from "../../components/HowItWorksStep";

const howItWorksSteps = [{
  step: 1,
  title: "Purchase an NFT",
  description: "You purchase helps fund your chosen project"
}, 
{
  step: 2,
  title: "UST is locked in a vault and accrues interest",
  description: "Enjoy the content and tiered rewards"
},
{
  step: 3,
  title: "The Project you supported is financed and the face value of your NFT can be redeemed",
  description: "At the completion of the project you decide"
}
];

const HowItWorks = () => {
  return (
    <PageSection flex id="how-it-works">
      <Box sx={{ 
          my: 4,
          marginTop: "100px ", 
          width: "100%", 
          marginBottom: "150px",
          ['@media(min-width: 780px)'] : {
            paddingLeft: "100px",
            paddingRight: "100px"
          }
        }}>
        <TypographyTitle kind={TYPOGRAPHY_TITLE_KIND.Secondary} sx={{textAlign: "center"}}>
          How it Works
        </TypographyTitle>
        <Box sx={{ mt: 12, display: "flex", justifyContent: "center"}}>
          <Box>
            <Grid container spacing="172">
              <Grid item  xs={12} md={6}>
                <TypographyTitle kind={TYPOGRAPHY_TITLE_KIND.Secondary} sx={{marginBottom: "60px"}}>
                  Crowdfunding Evolved
                </TypographyTitle>
                <Typography sx={{
                  fontSize: "22px",
                  color: "white",
                  lineHeight: "30px",
                }}>
                  Pixy eliminates risk for users by flipping the<br></br>
                  existing model. Users on the platform back<br></br>
                  a project by purchasing principal protected<br></br>
                  NFTs which are instantly sellable / tradable.<br></br>
                  At the completion of the project a burn<br></br>
                  window opens and each NFT is redeemable<br></br>
                  for its face value.
                </Typography>
              </Grid>
              <Grid item xs={12} md={6}>
                <TypographyTitle kind={TYPOGRAPHY_TITLE_KIND.Secondary} sx={{visibility: "hidden", marginBottom: "60px"}}>
                  Crowdfunding Evolved
                </TypographyTitle>
                <Typography sx={{
                  fontSize: "22px",
                  color: "white",
                  lineHeight: "30px",
                }}>
                  Your capital is protected with Pixy, ensuring<br></br>
                  a riskless experience with no wait for<br></br>
                  rewards
                </Typography>
              </Grid>
            </Grid>
          </Box>
        </Box>
        <Box sx={{ mt: 12, display: "flex", justifyContent: "center"}}>
          <Box>
            <Grid container spacing="22">
              {howItWorksSteps.map(({ title, ...step }) => (
                <Grid item xs={12} md={4}>
                  <HowItWorksStep key={title} title={title} {...step} />
                </Grid>
              ))}
            </Grid>
          </Box>
        </Box>
      </Box>
    </PageSection>
  );
};

export default HowItWorks;
